<template>
  <div class="manager-invited-list list-container">
    <div v-if="loading" class="mask flex jc-center ai-center"><i class="icon-loading"></i></div>
    <template v-else>
      <div class="tt flex jc-between ai-center text fs-18">{{ language.invited }}</div>
      <div class="row flex jc-between ai-center">
        <span class="c1">{{ language.address }}</span>
        <span class="c2">{{ language.label_6_6 }} (USDT)</span>
        <span class="c3">{{ language.label_5_1 }} (USDT)</span>
        <span class="c3 ta-right">{{ language.label_6_7 }}</span>
      </div>
      <div class="content">
        <template v-if="list.length">
          <div class="row flex jc-between ai-center text" v-for="item in list" :key="item.id">
            <span class="c1">{{ addressFormat(item.id) }}</span>
            <span class="c2">{{ amountFormatter(item.tradeAmount, 4) }}</span>
            <span class="c3">{{ amountFormatter(item.brokerRewardFromInvited, 4) }}</span>
            <div class="c3 ta-right">
              <div class="flex jc-end" v-if="editable">
                <label v-if="editingId===item.id" class="flex ai-center relative">
                  <input type="number" :ref="item.id" v-model="inputRate" min="0" max="100"
                         :class="rateInvalid(inputRate)?'success':'warning'"
                         :onblur="blurHandler"
                  >
                  <span class="suffix">%</span>
                  <img class="rate-btn" src="../../images/broker/confirm@2x.png" @click="setRewardRate(item)" alt="">
                </label>
                <label v-else class="flex ai-center normal" @click="click2Edit(item.id,item.rewardRate)">
                  <span>{{ item.rewardRate }}%</span>
                  <img class="rate-btn" src="../../images/broker/percent@2x.png" alt="">
                </label>
              </div>
              <span v-else>{{ rewardRate }}%</span>
            </div>
          </div>
        </template>
        <div v-else class="no-data flex jc-center ai-center">{{ language.no_data }}</div>
      </div>
    </template>
  </div>
</template>

<script>
import {mapGetters, mapState} from "vuex";
import {addressFormat, amountFormatter} from "@/common/dataConvertor";

export default {
  name: "managerInvitedList",
  props: {
    editable: {type: Boolean, default: false},
    rewardRate: {type: String }
  },
  data() {
    return {
      list: [],
      loading: false,
      inputRate: '',
      editingId: '',
      editTimer: null,
      editLoading: false,
    }
  },
  computed: {
    ...mapGetters(['language']),
    ...mapState('app', ['address']),
  },
  methods: {
    addressFormat,
    amountFormatter,
    getSubUserList() {
      this.loading = true;
      this.$api.getSubUserList(this.address).then(({users}) => {
        this.list = users;
        this.loading = false;
      }).catch(() => this.loading = false);
    },
    click2Edit(id, rate) {
      this.inputRate = rate;
      this.editingId = id;
      this.$nextTick(() => this.$refs[id].focus())
    },
    rateInvalid(rate) {
      return rate ? rate >= 0 && rate <= 100 : false;
    },
    blurHandler() {
      this.editTimer = setTimeout(() => {
        if (!this.editLoading) this.editingId = '';
      }, 200);
    },
    setRewardRate(item) {
      clearTimeout(this.editTimer);
      let {inputRate, address} = this;
      if (!this.rateInvalid(inputRate)) return;
      let {id} = item;
      this.editLoading = true;
      this.$api.setSubUserRewardRate(address, id, inputRate).then(() => {
        item.rewardRate = inputRate;
        this.editLoading = false;
        this.$refs[id].blur();
      }).catch(() => {
        this.editLoading = false;
      })
    }
  },
  mounted() {
    this.getSubUserList();
  },
}
</script>

<style lang="less" scoped>
.list-container {
  height: 100%;
  padding: 10px 0 0;
  background-color: #191f2d;
  border-radius: 10px;
  overflow: hidden;
  position: relative;

  .tt {
    height: 40px;
    padding: 0 25px;
  }

  .row {
    height: 40px;
    padding: 0 25px;

    .c1 {
      width: 30%;
    }

    .c2 {
      width: 30%;
    }

    .c3 {
      width: 25%;
    }

    .c4 {
      width: 40%;
    }
  }

  .content {
    height: calc(100% - 80px);
    overflow: auto;

    .row {
      &:hover {
        background-color: #353B4D;
      }
    }
  }

  label {
    &.normal {
      cursor: pointer;
    }
  }

  input {
    width: 75px;
    height: 30px;
    border: 1px solid transparent;
    padding: 0 35px 0 10px;
    text-align: right;
    border-radius: 10px;
    background-color: #292D34;
    position: relative;
    + .suffix {
      position: absolute;
      line-height: 30px;
      top: 50%;
      right: 45px;
      transform: translateY(-50%);
    }

    &.warning {
      border-color: #EC612B;
    }

    &.success {
      border-color: #2BEC93;
    }
  }

  .rate-btn {
    margin-left: 5px;
    cursor: pointer;
  }

  .no-data {
    height: 100%;
  }
}

@media screen and (max-width: 540px) {
  .list-container {
    .tt {
      padding: 0 15px;
    }

    .row {
      padding: 0 15px;
    }
  }
}
</style>