<template>
  <div class="manager page container-full flex flex-column">
    <div class="flex f1">
      <div class="join-box box flex1 flex jc-around">
        <img class="poster" src="../images/broker/bg-1@2x.png" alt="">
        <div class="article flex flex-column">
          <img class="poster-mobile" src="../images/broker/bg-1@2x.png" alt="">
          <div class="flex1 flex ai-center enter-btn-mobile">
            <div class="enter-btn-mobile-box" v-if="managerGrade===3">
              <p class="joined-num text flex jc-center ai-center">{{language.tip_6_5}}</p>
              <div class="join-btn redeem flex jc-center ai-center" @click="showDialog = true">
                <span class="gold fs-18">{{ language.redeem }}</span>
              </div>
            </div>
            <div v-else-if="managerGrade>0" class="text">
              <span class="fs-16">{{language.tip_6_6}}</span>
              <p class="my-super">{{ addressFormat(managerInfo.superior) }}</p>
            </div>
            <div class="enter-btn-mobile-box" v-else>
              <p class="joined-num text flex jc-center ai-center"></p>
              <div class="join-btn flex jc-center ai-center" @click="showDialog = true">
                <span class="fs-18">{{ language.label_6_1 }}</span>
              </div>
            </div>
          </div>
          <h2 class="gold fs-20">{{ language.tt_5_1 }}</h2>
          <p class="tips">
            * {{language.tip_6_1}}<br>
            * {{language.tip_6_2}}<br>
            * {{language.tip_6_3}}<br>
            * {{language.tip_6_4}}<br>
          </p>
          <div class="flex1 flex ai-center enter-btn-pc">
            <div v-if="managerGrade===3">
              <p class="joined-num text flex jc-center ai-center">{{language.tip_6_5}}</p>
              <div class="join-btn redeem flex jc-center ai-center" @click="showDialog = true">
                <span class="gold fs-18">{{ language.redeem }}</span>
              </div>
            </div>
            <div v-else-if="managerGrade>0" class="text">
              <span class="fs-16">{{language.tip_6_6}}</span>
              <p class="my-super">{{ addressFormat(managerInfo.superior) }}</p>
            </div>
            <div v-else>
              <p class="joined-num text flex jc-center ai-center"></p>
              <div class="join-btn flex jc-center ai-center" @click="showDialog = true">
                <span class="fs-18">{{ language.label_6_1 }}</span>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="bind-box box flex flex-column" :class="'grade-'+managerGrade">
        <h2 class="text fs-20">{{ language.invitation }}</h2>
        <template v-if="managerGrade===0">
          <p class="input-label text">{{ language.tt_5_2 }}</p>
          <label class="input-row input-el flex ai-center">
            <input class="flex1" type="text" v-model="bindingSuperior" :placeholder="language.tip_5_10">
          </label>
          <p>* {{language.tip_6_7}}</p>
          <div class="bind-row flex1 flex jc-center ai-center">
            <m-button v-if="addressRex(bindingSuperior)" class="bind-btn hdr-el-blue text" @click="bindSuperior"
                      :height="36"
                      :loading="bindLoading"
            >
              {{language.label_6_2}}
            </m-button>
            <m-button v-else class="bind-btn hdr-el-gray gray" :height="36" :disabled="true">
              {{language.label_6_2}}
            </m-button>
          </div>
        </template>
<!--        <template v-if="managerGrade===1">-->
<!--          <p class="input-label text">您已帮助好友获得收益</p>-->
<!--          <div class="dft-usdt">-->
<!--            <p class="flex ai-baseline">-->
<!--              <b class="dft-amount text fs-20">{{ 201 }}</b>-->
<!--              <span>DFT</span>-->
<!--            </p>-->
<!--            <p>≈ <span class="usdt-amount text">{{ 165259269 }}</span> USDT</p>-->
<!--          </div>-->
<!--        </template>-->
        <div v-show="managerGrade>1">
          <div class="sub-tt">{{language.tip_6_8}}</div>
          <p class="input-label text">{{ language.label_5_13 }}</p>
          <div class="input-row flex ai-center">
            <label class="input-el flex1 flex ai-center">
              <input class="flex1" type="text" readonly v-model="invitationLink">
            </label>
            <m-button class="copy-btn hdr-el-blue text" ref="copy" :data-clipboard-text="invitationLink" :height="36">
              {{ language.label_6_3 }}
            </m-button>
            <div class="copy-tip text animate-inout" :class="{show:copiedTipShow}">{{language.copy_tip}}</div>
          </div>
        </div>
      </div>
    </div>
    <ul v-if="managerGrade!==1" class="u1 flex jc-between">
      <li class="item-box">
        <div class="header flex jc-between ai-center">
          <span class="text">{{ language.invited }}</span>
        </div>
        <div class="flex ai-center">
          <b class="amount text">{{ invitationInfo.invtedUserTotal }}</b><span class="">{{ language.suffix_1 }}</span>
        </div>
      </li>
      <li class="item-box">
        <div class="header flex jc-between ai-center">
          <span class="text">{{ language.label_6_4 }}</span>
        </div>
        <div class="flex ai-center">
          <b class="amount text">{{ amountFormatter(invitationInfo.claimedBrokerRewardAmount) }}</b> <span>DFT</span>
        </div>
      </li>
      <li class="item-box">
        <div class="header flex jc-between ai-center">
          <span class="text">{{ language.label_5_1 }}</span>
          <m-button v-if="invitationInfo.unclaimedReward==='0'" class="item-btn hdr-el-gray gray" :disabled="true"
                    :height="36">
            {{ language.btn_reap }}
          </m-button>
          <m-button v-else class="item-btn hdr-el-green border-green green"
                    @click="reap" :loading="reapLoading" :height="36">
            {{ language.btn_reap }}
          </m-button>
        </div>
        <div class="flex ai-center">
          <b class="amount text">{{ amountFormatter(invitationInfo.unclaimedReward) }}</b> <span>DFT</span>
        </div>
      </li>
    </ul>
    <div class="list-box flex1">
      <!--   邀请的好友-->
      <manager-invited-list v-if="managerGrade>1" :rewardRate="invitationInfo.rewardRate" :editable="managerGrade===3"/>
      <!--   全网合伙人排行榜-->
      <all-rank-list v-else/>
    </div>
    <m-popper v-model="showDialog">
      <template v-slot:title>
        {{ managerInfo.isBroker ? language.tt_5_3 : language.tt_5_1 }}
      </template>
      <div class="main-box">
        <p v-if="!managerInfo.isBroker" class="balance-row flex jc-between ai-center">
          <span>{{ language.wallet_balance }}：<span class="text">{{ balanceFormatter(balance.DFT) }}</span></span>
          <span v-if="!balanceEnough" class="red">{{ language.label_6_5}}</span>
        </p>
        <!--        输入框-->
        <div class="input-bar">
          <p class="flex jc-between ai-center">
            <b class="flex ai-center text fs-16">
              <img class="coin icon-s" :src="coinMap.DFT" alt=""><span>DFT</span>
            </b>
            <b class="text fs-18">{{ requiredAmount }}</b>
          </p>
        </div>
        <div v-if="managerInfo.isBroker" class="action-tip">
          * {{language.tip_6_9}} <br>
          * {{language.tip_6_10}}
        </div>
        <p class="btn-row flex jc-center">
          <template v-if="isAllowed">
            <m-button v-if="managerInfo.isBroker" class="action-btn hdr-el-red border-red red" :height="36"
                      @click="doAction"
                      :loading="actionLoading">
              {{ language.redeem }}
            </m-button>
            <m-button v-else-if="balanceEnough" class="action-btn hdr-el-blue text" :height="36" @click="doAction"
                      :loading="actionLoading">
              {{ '入驻' }}
            </m-button>
            <m-button v-else class="action-btn hdr-el-gray border-gray gray" :height="36" :disabled="true">
              {{ '入驻' }}
            </m-button>
          </template>
          <m-button v-else class="action-btn hdr-el-blue text" :height="36" @click="doAllowance"
                    :loading="actionLoading">
            {{ language.btn_authorize }}
          </m-button>
        </p>
      </div>
    </m-popper>
  </div>
</template>

<script>

import MButton from "@/components/mButton";
import {mapGetters, mapState} from "vuex";
import {addressFormat, amountFormatter, balanceFormatter, addressRex} from "@/common/dataConvertor";
import MPopper from "@/components/mPopper";
import {coinMap} from "@/common/walletConfig";
import ClipboardJS from "clipboard";
import AllRankList from "@/views/managerComps/allRankList";
import ManagerInvitedList from "@/views/managerComps/managerInvitedList";

export default {
  name: "manager",
  components: {ManagerInvitedList, AllRankList, MPopper, MButton},
  watch: {
    address() {
      this.getAllInfo();
    }
  },
  data() {
    return {
      coinMap,
      requiredAmount: 10000,
      clipboard: null,
      copiedTipShow:false,
      managerInfo: {
        superior: '',
        isBroker: false,
        isBind: false,
        rewardRate: '0',
        managerGrade: 0
      },
      invitationInfo: {
        invtedUserTotal: '0',
        unclaimedReward: '0',
        rewardRate: '0',
        claimedBrokerRewardAmount: '0'
      },
      isAllowed: false,
      showDialog: false,
      actionLoading: false,
      allRankList: [],
      bindingSuperior: '',
      bindLoading: false, //帮得上操作loading
      reapLoading: false, //收割操作loading
    }
  },
  computed: {
    ...mapGetters(['language']),
    ...mapState('app', ['address', 'balance']),
    managerGrade() {
      return this.managerInfo.managerGrade
    },
    invitationLink() {
      return `${location.origin}/#/manager?superior=${this.address}`
    },
    balanceEnough() {
      return this.balance.DFT.toDisplay('balance').gte(this.requiredAmount);
    }
  },
  methods: {
    addressFormat,
    amountFormatter,
    balanceFormatter,
    addressRex,
    getAllInfo() {
      if (!this.address) return;
      this.checkAllowance();
      this.$api.getManagerInfo(this.address).then((managerInfo) => {
        this.managerInfo = managerInfo;
        console.debug(managerInfo)
        // managerInfo.managerGrade = 3;
        let {managerGrade} = managerInfo;
        if (managerGrade >= 2) {
          this.getInvitationInfo();
        }
      })
    },
    getInvitationInfo() {
      return this.$api.getManagerInvitationInfo(this.address).then((invitationInfo) => {
        this.invitationInfo = invitationInfo;
        console.debug(invitationInfo, 'invitationInfo')
      })
    },
    checkAllowance() {
      this.$api.managerCheckAllowance(this.address).then(num => this.isAllowed = num !== '0');
    },
    doAllowance() {
      this.actionLoading = true;
      this.$api.managerApprove(this.address).then(() => {
        this.isAllowed = true;
        this.actionLoading = false;
      }).catch(() => this.actionLoading = false);
    },
    doAction() {
      let isBroker = this.managerInfo.isBroker;
      this.actionLoading = true;
      (isBroker ? this.$api.quitManager(this.address) : this.$api.joinManager(this.address)).then(() => {
        this.actionLoading = false;
        this.getAllInfo();
        this.showDialog = false;
      }).catch(() => {
        this.actionLoading = false;
      });
    },
    readSuperior() {
      let {superior} = this.$route.query;
      this.bindingSuperior = superior || '';
    },
    bindSuperior() {
      let {address, bindingSuperior} = this;
      if (!address) return;
      this.bindLoading = true;
      this.$api.bindSuperior(address, bindingSuperior).then(() => {
        this.bindLoading = false;
        this.getAllInfo();
      }).catch(() => this.bindLoading = false);
    },
    reap() {
      let {address} = this;
      if (!address) return;
      this.reapLoading = true;
      this.$api.managerReap(address).then(() => {
        this.reapLoading = false;
        this.getAllInfo();
      }).catch(() => this.reapLoading = false);
    }
  },
  mounted() {
    this.getAllInfo();
    this.clipboard = new ClipboardJS(this.$refs.copy.$el);
    this.clipboard.on('success',()=>{
      this.copiedTipShow = true;
      setTimeout(()=> this.copiedTipShow = false,1000);
    })
    this.readSuperior();
  },
  unmounted() {
    this.clipboard.destroy();
    this.clipboard = null;
  }
}
</script>

<style lang="less" scoped>
.manager {
  color: #8C8F96;
}

.gold {
  color: #F4DAA9;
}

.input-row {
  margin: 10px 0 10px;
  position: relative;
  .copy-tip{
    position: absolute;
    line-height: 30px;
    padding: 0 10px;
    border-radius: 10px;
    right: 0;
    bottom: calc(100% + 5px);
    background-color: #323b55;
    opacity: 0;
    &.show{
      opacity: 1;
    }
  }
}

.input-el {
  height: 50px;
  padding: 0 20px;
  background-color: #353B4D;
  border-radius: 10px;
}

.box {
  min-height: 300px;
  background-color: #191f2d;
  border-radius: 10px;
  padding: 15px 20px;
  h2 {
    margin: 0 0 0;
    & ~ p {
      line-height: 1.6;
    }
  }
  .sub-tt {
    margin-top: 5px;
  }
  .tips {
    margin: 15px 0;
  }
}

.join-box {
  background-color: #1F1F1F;
  .poster {
    margin-top: 15px;
    height: 250px;
  }
  .poster-mobile {
    display: none;
  }
  .enter-btn-mobile{
    display: none;
  }
  .article {
    padding-left: 30px;
  }
  .joined-num {
    height: 15px;
    margin-bottom: 10px;
  }
  .join-btn {
    width: 200px;
    height: 50px;
    background: linear-gradient(0deg, #D8B783, #F4DAA9);
    border-radius: 20px;
    color: #2C2419;
    cursor: pointer;

    &.redeem {
      background: linear-gradient(0deg, #353637, #505152);
    }
  }
  .my-super {
    padding-top: 15px;
  }
}

.bind-box {
  width: calc((100% - 40px) / 3);
  margin-left: 20px;
  &.grade-1{
    display: none;
  }
  .bind-row{
    margin: 20px 0 10px;
  }
  .input-label {
    margin-top: 15px;
  }
  .dft-usdt {
    margin-top: 20px;
    line-height: 30px;
    .dft-amount {
      margin-right: 10px;
    }
    .usdt-amount {
      margin: 0 10px;
    }
  }
  .copy-btn {
    margin-left: 20px;
  }
}
.u1 {
  margin-top: 20px;
}
.item-box {
  width: calc((100% - 40px) / 3);
  padding: 0 25px;
  background: #191F2D;
  border-radius: 10px;
  position: relative;
  &:not(:last-child) {
    margin-right: 20px;
  }
  .header {
    height: 60px;
  }
  .item-btn {
    width: 90px;
  }
  .amount {
    line-height: 65px;
    font-size: 25px;
    & + span {
      margin-left: 5px;
    }
  }
}

.list-box {
  margin-top: 20px;
  height: 100%;
  min-height: 300px;
}

.main-box {
  min-width: 300px;
  padding: 10px 0 20px;
  .balance-row {
    margin-bottom: 10px;
  }
  .input-bar {
    padding: 10px 15px;
    border-radius: 10px;
    background-color: #292D34;
  }
  .coin {
    margin-right: 15px;
  }
  .action-tip {
    margin-top: 15px;
    line-height: 1.8;
  }
  .btn-row {
    margin-top: 20px;
  }
  .action-btn {
    min-width: 80px;
  }
}

@media screen and (max-width: 1024px) {
  .manager{
    padding: 20px 30px;
  }
  .f1{
    flex-direction: column;
  }
  .bind-box{
    width: 100%;
    min-height: auto;
    margin-top: 20px;
    margin-left: 0;
  }
  .u1{
    flex-wrap: wrap;
    .item-box{
      width: calc((100% - 20px) / 2);
      margin-bottom: 20px;
      &:nth-child(2){
        margin-right: 0;
      }
      &:nth-child(3){
        width: 100%;
        margin-bottom: 0;
      }
    }
  }
}
@media screen and (max-width: 540px) {
  .manager{
    padding: 15px;
  }
  .box{
    padding: 10px 15px;
    .tips {
      margin: 10px 0;
    }
  }
  .join-box{
    min-height: auto;
    padding-bottom: 15px;
    .poster{
      display: none;
    }
    .poster-mobile{
      display: block;
      margin-top: 15px;
      margin-bottom: 15px;
      height: 250px;

    }
    .enter-btn-mobile{
      width: 100%;
      display: flex;
      margin-bottom: 28px;
      &-box{
        width: 100%;
      }
      .join-btn{
        width: 100%;
        height: 44px;
      }
    }
    .enter-btn-pc{
      display: none;
    }
    .article{
      padding-left: 0;
    }
  }
  .bind-box{
    margin-top: 10px;
  }
  .input-el{
    height: 40px;
    padding: 0 15px;
  }
  .u1{
    margin-top: 10px;
    .item-box{
      width: calc((100% - 10px) / 2);
      padding: 0 15px;
      margin-right: 10px;
      margin-bottom: 10px;
      &:nth-child(3){
        width: 100%;
        margin-right: 0;
        margin-bottom: 0;
      }
      .header{
        height: 40px;
      }
      .amount{
        font-size: 18px;
        line-height: 40px;
      }
      .item-btn{
        position: relative;
        top: 20px;
      }
    }
  }
  .list-box{
    margin-top: 10px;
  }
}
</style>