<template>
  <div class="all-rank-list list-container">
    <div v-if="loading" class="mask flex jc-center ai-center"><i class="icon-loading"></i></div>
    <template v-else>
      <div class="tt flex jc-between ai-center text fs-18">{{ language.tt_5_4 }}</div>
      <div class="row flex jc-between ai-center">
        <span class="c4">{{ language.label_5_9 + language.address }}</span>
        <span class="c2">{{ language.label_6_8 }}</span>
        <span class="c3 ta-right">{{ language.label_5_1 }}(USDT)</span>
      </div>
      <div class="content">
        <template v-if="list.length">
          <div class="row flex jc-between ai-center text" v-for="item in list" :key="item.id">
            <span class="c4">{{ addressFormat(item.id) }}</span>
            <span class="c2">{{ item.invtedUserTotal }}</span>
            <span class="c3 ta-right">{{ amountFormatter(item.brokerRewardAmount, 4) }}</span>
          </div>
        </template>
        <div v-else class="no-data flex jc-center ai-center">{{ language.no_data }}</div>
      </div>
    </template>
  </div>
</template>

<script>
import {addressFormat, amountFormatter, balanceFormatter} from "@/common/dataConvertor";
import {mapGetters} from "vuex";

export default {
  name: "allRankList",
  data() {
    return {
      list:[],
      loading:false,
    }
  },
  computed:{
    ...mapGetters(['language']),
  },
  methods:{
    addressFormat,
    amountFormatter,
    balanceFormatter,
    getRankList() {
      this.loading = true;
      this.$api.getManagerRankList().then(({users}) => {
        this.list = users;
        this.loading = false;
      }).catch(() => this.loading = false);
    },
  },
  mounted() {
    this.getRankList();
  }
}
</script>

<style scoped lang="less">
.list-container {
  height: 100%;
  padding: 10px 0 0;
  background-color: #191f2d;
  border-radius: 10px;
  overflow: hidden;
  position: relative;
  .tt {
    height: 40px;
    padding: 0 25px;
  }
  .row {
    height: 40px;
    padding: 0 25px;
    .c1 {
      width: 30%;
    }
    .c2 {
      width: 30%;
    }
    .c3 {
      width: 25%;
    }
    .c4 {
      width: 40%;
    }
  }
  .content {
    height: calc(100% - 80px);
    overflow: auto;
    .row {
      &:hover {
        background-color: #353B4D;
      }
    }
  }
  .no-data {
    height: 100%;
  }
}
@media screen and (max-width: 540px) {
  .list-container{
    .tt{
      padding: 0 15px;
    }
    .row{
      padding: 0 15px;
    }
  }
}
</style>